  color: #ffa358;
  margin-right: 10px;
}

.project-container:not(:last-of-type) {
  margin-bottom: 100px;
}

.project-container {
  position: relative;
  display: grid;
  grid-gap: 10px;
  gap: 10px;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
}

.project-container .project-content {
  grid-area: 1 / 1 / -1 / 7;
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  z-index: 5;
  padding: 25px 25px 20px;
}

.project-container:nth-of-type(2n + 1) .project-content {
  padding: 25px 25px 20px;
  grid-column: 1 / -1;
  text-align: left;
}

.project-container:nth-of-type(2n + 1) .project-techstack {
  justify-content: flex-end;
}

.project-container:nth-of-type(2n + 1) .project-link {
  justify-content: flex-end;
}

.project-container:nth-of-type(2n + 1) .project-image {
  grid-column: 1 / -1;
}

.project-container .project-image {
  grid-column-start: 1;
  grid-column-end: -1;
  grid-area: 1 / 1 / -1 / -1;
  height: 100%;
  opacity: 0.25;
  position: relative;
  z-index: 1;
}

@media (min-width: 768px) {
  .project-container .project-content {
    position: relative;
    grid-area: 1 / 1 / -1 / 7;
    padding: 0;
  }

  .project-container:nth-of-type(2n + 1) .project-content {
    grid-column: 7 / -1;
    text-align: right;
    padding: 0;
  }
  .project-container:nth-of-type(2n + 1) .project-image {
    grid-column: 1 / 8;
  }

  .project-container .project-image {
    box-shadow: 0 10px 30px -15px var(--navy-shadow);
    transition: var(--transition);
    grid-area: 1 / 6 / -1 / -1;
    position: relative;
    z-index: 1;
    opacity: 1;
  }
}

